import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import StockTradingScreen from "../screens/stock-trading";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";

const StockTradingPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Stock Trading App Development", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <StockTradingScreen db={db} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Stock Trading App Development Services"
    headTitle="Stock Trading App Development Company"
    description="Empower your financial journey with Justtry Technologies, a premier stock trading development company. Elevate your trading platform with our cutting-edge solutions, offering seamless, secure, and scalable trading experiences.
    "
    keywords="Stock Trading Platform Development, Custom Stock Trading Solutions, Algorithmic Trading Development, Stock Market Software Development, Trading Platform Integration, Financial Technology (FinTech) Solutions, Real-time Stock Market Data, Trading System Architecture, Order Execution Systems, High-Frequency Trading (HFT), Trading APIs and Integration, Back-Office Systems for Trading, Risk Management Solutions, Financial Data Feeds, Stock Market Analysis Tools, Trading Strategy Development, Compliance and Regulatory Solutions, Multi-Asset Trading Platforms, Options and Derivatives Trading, Cryptocurrency Trading Integration, Forex Trading Solutions, Liquidity Management, Trading Software Testing and QA, Portfolio Management Systems
    "
  ></Seo>
);

export default StockTradingPage;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
