import React, { useState } from "react";
import {Container, Accordion} from "react-bootstrap";
import Faq from "../../components/common/faq"

import student from "../../styles/assets/images/stocktrading/student_image.png";

import rocket from "../../styles/assets/images/stocktrading/rocket.svg";
import rocket_base from "../../styles/assets/images/stocktrading/rocket_base.svg";
import teach from "../../styles/assets/images/stocktrading/teaching.svg";
import keyfeature from "../../styles/assets/images/stocktrading/key_image.png";
import choose from "../../styles/assets/images/stocktrading/choose_image.png";
import test from "../../styles/assets/images/stocktrading/process_four.svg";

import TalkToUs from "../../components/common/talk-to-us";
import { yupResolver } from "@hookform/resolvers/yup";
import * as Yup from "yup";

import Loading from "../../components/common/loading";
import { useForm } from "react-hook-form";
import { Country } from "country-state-city";
import { collection, addDoc } from "firebase/firestore/lite";
import { utils } from "../../core/helper";
import "animate.css";


const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const StockTradingService = (props) => {
  const { db } = props;
  const [loading, setLoading] = useState(false);
  const [talkToUsShow, setTalkToUsShow] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const handleToggleTalkToUs = () => {
    setTalkToUsShow(!talkToUsShow);
    reset();
  };

  const countries = Country.getAllCountries();

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
      setTimeout(() => {
        handleToggleTalkToUs();
      }, 1000);
    } catch (e) {
      utils.showErrMsg("Please enter the valid details");
      setLoading(false);
    }
  };

  const [activeKey, setActiveKey] = useState(null);

  const toggleAccordion = (eventKey) => {
    setActiveKey(eventKey === activeKey ? null : eventKey);
  };

  const stockFaqData = [
    {
      question: "What features should a stock trading app have?",
      answer: "Essential features include real-time market data, intuitive user interface, secure transactions, and customizable alerts."
    },
    {
      question: "How can the app ensure user security?",
      answer: "Implement strong encryption, two-factor authentication, biometric login options, and regular security audits."
    },
    {
      question: "What technologies are crucial for app development?",
      answer: "Utilize reliable frameworks (e.g., React Native), APIs for market data, cloud hosting (AWS/Azure), and robust backend technologies."
    },
    {
      question: "How to comply with financial regulations?",
      answer: "Adhere to KYC (Know Your Customer) procedures, obtain necessary licenses, and stay informed about evolving financial regulations and compliance standards."
    }
  ];
  

  return (
    <div className="stock_trading_wrapper">
      <section className="stock_trading_hero_sec">
        <div className="stock_hero_sec_lhs">
          <div className="stock_hero_title">
            <h1>Stock Trading App Development</h1>
          </div>
          <div className="stock_hero_description">
            <p>
              Dive headfirst into the future of investing by launching a
              trailblazing stock trading app. Itâ€™s time to roll out the red
              carpet for investors worldwide.
            </p>
          </div>
          <div className="stock_btn_wrapper">
            <button type="submit" className="stock_btn" onClick={handleToggleTalkToUs}>
              TALK TO US
            </button>
          </div>
        </div>
        <div className="stock_hero_sec_rhs">
          {/* <img width={615} src={hero_image} alt="Learing stock image" /> */}
          {utils.stockTradingHeroIcon(615, 615)}
        </div>
      </section>{" "}
      <section className="modern_way_section stock_trading_custom_padding">
        <div className="modern_way_sec_lhs">
          <h1>Stock Trading App: The Modern Way of Trading</h1>
          <p>
            Trade has been a part of human civilization since the dawn of
            agriculture, although trading methods varied among different
            isolated communities. Barter systems were common, involving the
            exchange of goods and services. However, the lack of a standard
            measure of value made this cumbersome. The solution came in the form
            of money, which established a universal standard for assessing a
            product's worth. This breakthrough set in motion a series of
            economic and financial advancements, including the emergence of
            stock trading.{" "}
          </p>
          <p>
            The advent of high-speed internet and the increasing interest in
            financial literacy have transformed stock trading through mobile
            applications. As these apps continue to gain widespread usage, both
            startups and established organizations are investing in the
            development of stock trading applications. With the only
            prerequisites being a mobile device and an internet connection, it's
            evident that stock trading apps have the potential to attract a vast
            number of traders, making even newly launched trading apps
            successful. Partner with Justtry today and turn your vision of a
            stock trading app into reality!
          </p>
        </div>
        <div className="modern_way_sec_rhs">
          <div className="modern_way_image_wrapper">
            <img
              className="student_image"
              src={student}
              alt="Student_trading_image"
            />
            
          </div>
        </div>
      </section>
      <section className="launch_app_section stock_trading_custom_padding ">
        <div className="stock_trading_launch_wrapper">
          <div className="stock_trading_launch_lhs">
            <img
              className="first_launch"
              src={rocket_base}
              alt="app_launch_image"
            />
            <img
              className="second_launch"
              src={rocket}
              alt="app_launch_image"
            />
          </div>
          <div className="stock_trading_launch_rhs">
            <h2>
              Be the trailblazer of the digital era. Launch your custom stock
              trading app today!
            </h2>
            <div className="launch_btn_wrapper">
              <button type="submit" className="launch_btn" onClick={handleToggleTalkToUs}>
                Talk to us
              </button>
            </div>
          </div>
        </div>
      </section>
      <section className="stock_trading_app_works_section stock_trading_custom_padding">
        <div className="stock_trading_app_wrapper">
          <div className="stock_trading_app_tile">
            <h1>Decoding the Magic: How a Stock Trading App Works</h1>
          </div>
          <div className="stock__trading_app_content">
            <div className="stock_trading_app_lhs">
              <p>
                In essence, a stock trading app serves as the gateway for
                individuals to participate in the dynamic world of stock
                markets. Users simply download the app onto their smartphones to
                embark on a journey of investing in stocks, whether it's for the
                long haul or short-term gains. Beyond just investing, these apps
                provide real-time market tracking capabilities, keeping users
                updated on stock prices and market trends directly from the palm
                of their hand.
              </p>
            </div>
            <div className="stock_trading_app_center">
              <img src={teach} alt="trader_teaching_app_working" />
            </div>
            <div className="stock_trading_app_rhs">
              <p>
                Behind the scenes, brokers play a pivotal role in facilitating
                these stock exchanges through these stock trading platforms.
                They act as intermediaries, executing trades on behalf of users
                and ensuring the smooth flow of transactions. In return, brokers
                generate revenue from commissions and fees associated with these
                trades, contributing to the sustainability of the stock trading
                ecosystem. So, in a nutshell, a stock trading application is not
                just a tool for investment, it's a gateway to financial
                opportunities, with brokers at the heart of the action.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="stock_trading_process_section stock_trading_custom_padding">
        <div className="stock_trading_process_header">
          <h1>
            Systematic Blueprint of Our Stock Trading App <br />
            Development Process
          </h1>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h1>01</h1>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockIdResearchIcon(280, 196)}
          </div>
          <div className="stock_trading_dev_process">
            <h3>Ideation and Research</h3>
            <p>
              Before embarking on any project, thorough planning is paramount.
              This phase involves defining the requirements, features, and
              target audience for your stock trading application. In addition,
              our expert consultants collaborate with you to finalize a
              marketing strategy and monetization model to ensure your trading
              app's profitability.
            </p>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_develop_process">
            <h3>Design</h3>{" "}
            <p>
              Our seasoned team of UX designers transforms your innovative
              concepts into visual representations using graphical designs. If
              required, they can also create dynamic prototypes for testing or
              proof of concept, providing insights into your app's market
              potential before launch.
            </p>
          </div>
          <div className="stock_trading_develop_process">
            {utils.stockDesignIcon(280, 186)}
          </div>
          <div className="stock_trading_develop_process">
            <h1>02</h1>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h1>03</h1>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockDevIcon(280, 280)}
          </div>
          <div className="stock_trading_dev_process">
            <h3>Development</h3>
            <p>
              This pivotal stage involves converting designs into code. Our
              developers, well-versed in your project's requirements, utilize
              the perfect technology stack for front-end and back-end
              development and initiate coding. They also advise on integrating
              third-party APIs to enhance performance and cybersecurity.
            </p>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_develop_process">
            <h3>Testing</h3>
            <p>
              Quality assurance is paramount. Our QA testers conduct
              comprehensive end-to-end testing to eliminate bugs and fortify
              your stock trading app against DDoS attacks, ensuring a smooth
              user experience.
            </p>
          </div>
          <div className="stock_trading_develop_process">
            <img
              width={280}
              height={196.3}
              src={test}
              alt="trade app process explanations"
            />
          </div>
          <div className="stock_trading_develop_process">
            <h1>04</h1>
          </div>
        </div>
        <div className="stock_trading_process">
          <div className="stock_trading_dev_process">
            <h1>05</h1>
          </div>
          <div className="stock_trading_dev_process">
            {utils.stockDeployIcon(280, 196)}
          </div>
          <div className="stock_trading_dev_process">
            <h3>Deployment and Maintenance</h3>
            <p>
              With a fully functional stock trading app, it's time to launch and
              market it. We create targeted marketing campaigns to communicate
              your app's unique value proposition to your audience. After
              generating interest, we assist in deploying your app on cloud
              servers or locally-hosted systems. Post-launch, we actively gather
              user feedback and provide ongoing updates and maintenance to meet
              your evolving needs.
            </p>
          </div>
        </div>
      </section>
      <section className="stock_trading_feature_section stock_trading_custom_padding">
        <div className="stock_trading_feature_top">
          <h1>Key Features of Our Stock Trading Application</h1>
        </div>
        <div className="stock_trading_feature_bottom">
          <div className="stock_trading_feature_img_wrapper">
            <img
              src={keyfeature}
              alt="Man explains the feature in stock trading"
            />
          </div>
          <div className="stock_trading_accordion">
            <Accordion activeKey={activeKey} flush>
              <Accordion.Item
                eventKey="0"
                className={`accordion-item ${
                  activeKey === "0" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("0")}>
                  User Profile Dashboard
                </Accordion.Header>
                <Accordion.Body>
                  A customizable dashboard for users to manage their profiles
                  and monitor trading activities on the go.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="1"
                className={`accordion-item ${
                  activeKey === "1" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("1")}>
                  Newsfeed
                </Accordion.Header>
                <Accordion.Body>
                  Timely information is crucial when it comes to stock trading.
                  For this purpose, we include a curated news feed for accurate
                  updates and insights.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="2"
                className={`accordion-item ${
                  activeKey === "2" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("2")}>
                  Secure Login (Authentication)
                </Accordion.Header>
                <Accordion.Body>
                  Just like any other mobile banking app, users need to create
                  accounts and confirm their identities to comply with KYC
                  regulations. Two-factor authentication adds an extra layer of
                  security.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="3"
                className={`accordion-item ${
                  activeKey === "3" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("3")}>
                  Analytics
                </Accordion.Header>
                <Accordion.Body>
                  This provides trading charts, indicators, and analytical data
                  to help users make informed decisions.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="4"
                className={`accordion-item ${
                  activeKey === "4" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("4")}>
                  Custom Search (Filters)
                </Accordion.Header>
                <Accordion.Body>
                  This feature makes it easy for users to find specific stocks
                  and assets quickly.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="5"
                className={`accordion-item ${
                  activeKey === "5" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("5")}>
                  Favorites
                </Accordion.Header>
                <Accordion.Body>
                  This allows users to save their preferred stocks and assets
                  for easy access.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="6"
                className={`accordion-item ${
                  activeKey === "6" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("6")}>
                  Push Notifications
                </Accordion.Header>
                <Accordion.Body>
                  Notifications keep users informed with real-time market
                  updates and asset volatility alerts.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="7"
                className={`accordion-item ${
                  activeKey === "7" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("7")}>
                  Limit Orders
                </Accordion.Header>
                <Accordion.Body>
                  Supports automated trading strategies with limit order
                  functionalities like "Take Profit" and "Stop Limit" to manage
                  risk.
                </Accordion.Body>
              </Accordion.Item>
              <Accordion.Item
                eventKey="8"
                className={`accordion-item ${
                  activeKey === "8" ? "accordion_item_active" : ""
                }`}
              >
                <Accordion.Header onClick={() => toggleAccordion("8")}>
                  User-Friendly Interface
                </Accordion.Header>
                <Accordion.Body>
                  Our stock trading platform has been seamlessly designed for
                  beginners and experts alike.
                </Accordion.Body>
              </Accordion.Item>
            </Accordion>
          </div>
        </div>
      </section>
      <section className="stock_trading_choose_justtry stock_trading_custom_padding">
        <div className="stock_trading_choose_lhs">
          <h1>
            Why Justtry is Your Ultimate Choice for Stock Trading App
            Development
          </h1>
          <p>
            At Justtry, Your Satisfaction is Our Priority. Our team of seasoned
            professionals specializes in stock trading app development, equipped
            with a deep understanding of the intricacies of financial markets to
            ensure your app meets industry standards. We remain at the forefront
            of technology trends, infusing your app with cutting-edge features
            to keep it competitive and responsive to market shifts. Our
            unwavering commitment extends well beyond the development phase. We
            offer continuous support, addressing issues, rolling out updates,
            and fine-tuning performance to ensure your app's enduring success.
            Leverage our al-inclusive stock trading app development services
            today!
          </p>
        </div>
        <div className="stock_trading_choose_rhs">
          <img src={choose} alt="girl_ui_image_indicates choose justtry" />
          <div className="stock_trading_ellipse"></div>
        </div>
      </section>
      <Container>
        <Faq faqData={stockFaqData}/>
      </Container>
      <TalkToUs
        show={talkToUsShow}
        onHide={handleToggleTalkToUs}
        closeButton={true}
        size={"lg"}
      >
        <div className="screen__content">
          <form onSubmit={handleSubmit(handleOnSubmit)} className="login">
            <div className="login__field">
              <i className="login__icon fas fa-user"></i>
              <input
                type="text"
                className="login__input"
                {...register("name")}
                placeholder="Your Name"
              />
              <p className="text-danger mb-0">{errors.name?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <input
                type="email"
                className="login__input"
                {...register("email")}
                placeholder="Email"
              />
              <p className="text-danger mb-0">{errors.email?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <input
                type="text"
                className="login__input"
                placeholder="Skype / Mobile"
                {...register("mobile")}
              />
              <p className="text-danger mb-0">{errors.mobile?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <select className="login__input" {...register("country")}>
                <option value="">Select</option>
                {countries.map((ele, ind) => {
                  return (
                    <option key={ind} value={ele.isoCode}>
                      {ele.name}
                    </option>
                  );
                })}
              </select>
              <p className="text-danger mb-0">{errors.country?.message}</p>
            </div>
            <div className="login__field">
              <i className="login__icon fas fa-lock"></i>
              <textarea
                id="content"
                placeholder="Type content here"
                rows={5}
                className="login__input"
                {...register("content")}
                maxLength={200}
              ></textarea>
              <p className="text-danger mb-0">{errors.content?.message}</p>
            </div>
            <button type="submit" disabled={loading} className="button login__submit">
              <span className="button__text">
                {" "}
                Submit {loading && <Loading />}
              </span>
              <i className="button__icon fas fa-chevron-right"></i>
            </button>
          </form>
        </div>
        <div className="screen__background">
          <span className="screen__background__shape screen__background__shape4"></span>
          <span className="screen__background__shape screen__background__shape3"></span>
          <span className="screen__background__shape screen__background__shape2"></span>
          <span className="screen__background__shape screen__background__shape1"></span>
        </div>
      </TalkToUs>
    </div>
  );
};

export default StockTradingService;
